* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

li {
    list-style: none;
}

a {
    color: inherit;
    text-decoration: none;
}

body {
    line-height: 1;
    color: #333;
}

.header {
    height: 100px;
}
.header .inner {
    max-width: 1280px;
    width: calc(100% - 30px);
    height: 100%;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.nav .gnb {
    display: flex;
    gap: 30px;
}
.nav_open  {
    width: 20px;
}
.nav_open {
    position: relative;
    z-index: 99;
}
.nav_open img {
    width: 100%;
}
.all_nav {
    width: 50%;    
    height: 100vh;
    position: fixed;
    right: -100%;
    top: 0;
    background-color: #167758;
    color: white;
    transition: 0.7s;
}
.all_nav.on {
    right: 0;
}